body { 
	text-align: center; 
	font-family: arial;
}

#root {
	margin: auto;
    width: 600px;
    padding: 10px;
    border: 5px ridge gray;
    border-radius: 10px;
}


ul {
    list-style-type: none;
    padding: 20px;
}

li {
	text-align: left;
	padding-left: 5px;
	padding-right: 5px;
	border: 5px ridge gray;
    border-radius: 10px;
}
p {
	display: inline-block;
}

.completedClass {
	background: #E0E0E0;
}

#text {
	text-align: left;
}

#btnBox {
	float: right;
}

#itemTextBox {
	width: 400px;
	font-size: 16px;
}

button {
	width: 50px;
	padding: 2px;
    background-color: white;
    color: black;
    border-radius: 4px;
}

button:hover {
    background-color: #555555;
    color: white;
}

#addItemBtn{
	width: 80px;
}

.addItem {
	overflow: hidden;
}

#left {
	float: left;
	padding-left: 50px;
}

#right {
	float: right;
	padding-right: 50px; 
}






